				"html": "<p>Sometimes you will need to test more than one thing at a time. The <code>logical and</code> operator (<code>&#x26;&#x26;</code>) returns <code>true</code> if and only if the <code>operands</code> to the left and right of it are true.</p>\n<p>The same effect could be achieved by nesting an if statement inside another if:</p>\n<pre><code>if (num > 5) {\n  if (num &#x3C; 10) {\n    return \"Yes\";\n  }\n}\nreturn \"No\";\n</code></pre>\n<p>will only return \"Yes\" if <code>num</code> is between <code>6</code> and <code>9</code> (6 and 9 included). The same logic can be written as:</p>\n<pre><code>if (num > 5 &#x26;&#x26; num &#x3C; 10) {\n  return \"Yes\";\n}\nreturn \"No\";\n</code></pre>",
